import React from "react";
import { PickerColumn } from "@ionic/core";
import { IonPicker } from "@ionic/react";
import { useTranslation } from "react-i18next";

/**
 * the component has three properties that are defined in this 
 * interface since we are using typescript
 */
interface _Props {
  isOpen: boolean
  onSave: Function
  onCancel: Function
}

const ResponseMembersPicker: React.FC<_Props> = ({ onSave, onCancel, isOpen }) => {
  const { t } = useTranslation();
  const DayColumn = {
    name: "Day",
    options: [
      { text: "", value: "all_groups" },
      { text: "", value: "info" },
      { text: "", value: "monitor" },
      { text: "", value: "action" },
      { text: "", value: "product" },
      { text: "", value: "emergency" },
    ]
  } as PickerColumn;

  const SessionTimeColumn = {
    name: "SessionTime",
    options: [
      { text: "", value: "all_jobs" },
      { text: "", value: "search" },
      { text: "", value: "collation" },
      { text: "", value: "verification" },
      { text: "", value: "evaluation" },
      { text: "", value: "follow" },
      { text: "", value: "hydro_monitor" },
      { text: "", value: "meteoro_monitor" },
      { text: "", value: "liaison" },
      { text: "", value: "map" },
      { text: "", value: "report" },
    ]
  } as PickerColumn;
  return (
    <div>
      <IonPicker
        isOpen={isOpen}
        columns={[DayColumn, SessionTimeColumn]}
        buttons={[
          {
            text: t("response.cancel"),
            role: "cancel",
            handler: value => {
              onCancel()
            }
          },
          {
            text: t("response.confirm"),
            handler: value => {
              onSave(value)
            }
          }
        ]}
      ></IonPicker>
    </div>
  );
};

export default ResponseMembersPicker;
